.SliderWrap{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
}

.Slider{
    display: flex;
    width: 50%;
    justify-content: space-between;
   animation: slide 0.3s ease-in-out forwards;
   border-radius: 10px;
    padding: 10px;
}

.sliderCompany img{
    max-width: 42px;
    max-height: 42px;
}

.sliderBtn {
    border: none;
    outline: none;
    background: #364F6B;
    margin-top: 32px;
}

.sliderBtn img{
    max-width: 34px;
    max-height: 34px;

}


@keyframes slide{
    0%{
       padding-left: 0px;
    }

    100%{
       padding-left: 4px;
    }
}