.intro{
    display: flex;
    margin: 60px 0px;
    gap:120px;
    justify-content: space-between;
}

.intro h1{
    font-family: 'Lato', sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 72px;
    line-height: 86px;
    
    color: #364F6B;
}

.intro p{
    font-family: 'Lato';
font-style: normal;
font-weight: 400;
font-size: 16px;
line-height: 28px;
color: #333333;
margin: 30px 0px;
}

.intro button{
    font-family: 'Lato';
background: #43DDE6;
border-radius: 100px;
outline: none;
border: none;
padding: 10px 42px;
font-style: normal;
font-weight: 600;
font-size: 16px;
line-height: 24px;
/* identical to box height */


color: #000000;

}

.intro_img img{
    width: 500px;
height: 470px;
border-radius: 16px;

}

.trusted{
    padding: 10px 40px;
    text-align: center;
   color:white;
background: #364F6B;
border-radius: 32px;
}

.trustedHeading{
    display: flex;
    justify-content: center;
    align-items: center;
    gap:28px;

}

.trustedHeading div{
    margin-bottom: 40px;
}

.trusted h2{
    margin:40px 0px 80px 0px;
    font-size: 28px;
}

.serviceDesc{
    border-top: 1px solid white;
    display: grid;
    grid-template-columns: 60% 40%;
    justify-items: start;
    justify-content: space-evenly;    
    align-items: center;
    margin: 30px;
    padding: 50px;
}


.serviceDesc .serviceHeading p{
    font-family: 'Lato';
font-style: normal;
font-weight: 500;
font-size: 58px;
line-height: 86px;
text-align: start;
color: #FFFFFF;
}



.serviceDesc p{
    font-family: 'Lato';
font-style: normal;
font-weight: 500;
text-align: justify;
font-size: 16px;
line-height: 28px;
/* or 175% */


color: #D9D9D9;
}

.serviceGrid{
    display: flex;
}

.serviceGrid div{
    padding: 20px;
}

.serviceGrid img{
    max-width: 70px;
    max-height: 70px;
    align-items: center;
    justify-content: space-between;
}

.develop{
    border-left: 1px solid white;
    border-right: 1px solid white;
    border-radius: 2px;
}

.serviceGrid h3{
    margin: 10px 0px;
}

.serviceGrid p{
    margin: 16px 0px;
}

.ourWork{
    margin: 80px 0px;
}

.ourWork h2{
    font-family: 'Lato';
font-style: normal;
font-weight: 500;
font-size: 64px;
line-height: 86px;
margin-bottom: 40px;
color: #364F6B;
text-align: center;
}

.works{
display: grid;
grid-template-columns: 30% 30% 30%;
justify-content: center;
justify-items: center;
}

.works img{
    max-width: 200px;
    max-height: 200px;
}

.project{
width: 80%;
background: #43DDE6;
box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.15);
border-radius: 16px;
}

.project h3{
    padding: 10px;
    font-family: 'Poppins';
font-style: normal;
font-weight: 500;
font-size: 22px;
padding: 12px;
line-height: 36px;
/* identical to box height */


color: #000000;
}

.project p{
    padding: 12px;
    font-family: 'Poppins';
font-style: normal;
font-weight: 400;
font-size: 15px;
line-height: 24px;

color: #000000;
}

.projectDesc{
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 12px;
    background-color: #FFFFFF;
}

.projectDesc img{
    border:1px solid #333333;
    border-top-left-radius: 100%;
    
    border-bottom-left-radius: 100%;
    border-bottom-right-radius: 100%;

    padding: 10px;
}

.ourWorkbtn {
    margin-top: 46px;
    text-align:center;
}

.ourWork button{
    font-family: 'Lato';
font-style: normal;
font-weight: 600;
font-size: 16px;
background: #43DDE6;
border-radius: 100px;
padding: 10px 26px;
border: none;
line-height: 24px;
/* identical to box height */


color: #000000;
}