
.form-main h1{
    font-family: 'Lato';
font-style: normal;
font-weight: 500;
font-size: 64px;
margin: 20px;
line-height: 86px;

color: #364F6B;
}

.connect{
    display: flex;
    justify-content: space-around;
 
    margin-bottom: 50px;
}
.connect form{
    background: #364F6B;
    border-radius: 24px;
    padding: 36px;
    width: 54%;
}

.connect form h3{
    font-family: 'Poppins';
font-style: normal;
font-weight: 500;
font-size: 24px;

color: #FFFFFF;
}


.connect form p{
    font-family: 'Poppins';
font-style: normal;
width: 80%;
font-size: 15px;
margin: 8px 0px;
line-height: 28px;

/* or 175% */


color: #D9D9D9;
}

.connect form label p{font-family: 'Poppins';
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 27px;
    /* identical to box height */
    
    
    color: #FFFFFF;}


.connect form input{
    background: #394149;
border-radius: 8px;
width: 100%;
outline: none;
border: none;  font-family: 'Poppins';
padding: 10px;
margin: 10px 0px;
height: 40px;
font-size: 18px;
color:#FFFFFF;
}

.form_labels{
    margin-top: 28px;
}

.side-labels{
    display: flex;
    gap:20px;
}

.side-labels div{
    width: 100%;
   
}

.connect form textarea{
height:90px;
width: 100%;
outline: none;
border:none;
background: #394149;
border-radius: 8px;
font-size: 18px;
color:#FFFFFF;
padding: 10px;
font-family: 'Poppins';
}

.connect form button{
    font-family: 'Lato';
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    margin: 30px 0px;
    background: #43DDE6;
    border-radius: 100px;
    border: none;
    padding: 12px 48px;
    line-height: 24px;
    color: #333333;
}



.connect .contact h3{
    font-family: 'Poppins';
font-style: normal;
font-weight: 500;
font-size: 20px;
line-height: 42px;
/* identical to box height */


color: #333333;
}

.connect .contact .call-us{
  
    border-bottom: 2px solid rgba(51, 51, 51, 0.5);
   padding-bottom:28px;
   padding-top: 20px;

}

.connect .contact .address{
    border-bottom: 2px solid rgba(51, 51, 51, 0.5);
    padding-bottom:28px;
}

.connect .contact .social-media{
    margin-top: 20px;
}