.Footer{

    padding: 60px 80px 40px 80px;
    gap:60px;
    color: white;
    background: #364F6B;
    border-top-left-radius: 32px;
    border-top-right-radius: 32px;
}

.f1{
    display: flex;
gap:80px;
align-items: center;
    justify-content: space-between;
}

.Footer .f1 h1{
    font-family: 'Lato';
font-style: normal;
font-weight: 500;
font-size: 72px;

line-height: 86px;

color: #FFFFFF;
}

.f1 p{
    font-family: 'Lato';
font-style: normal;
font-weight: 500;
font-size: 16px;
line-height: 28px;


color: #D9D9D9;
}

.f1 button{
    font-family: 'Lato';
font-style: normal;
font-weight: 600;
font-size: 16px;
margin: 30px 0px;
background: #43DDE6;
border-radius: 100px;
border: none;
padding: 12px 48px;
line-height: 24px;

color: #333333;

}

.f2{
    width: 88%;
    display: flex;
    justify-content: space-between;
    margin: 62px 0px;
    
}

.f2 h3{
    font-family: 'Poppins';
font-style: normal;
font-weight: 500;
font-size: 18px;
line-height: 30px;
/* identical to box height */


color: #FFFFFF;

}

.f2 .f2_desc{
    font-family: 'Poppins';
font-style: normal;

font-size: 15px;
line-height: 30px;
/* identical to box height */


color: #FFFFFF;
    margin-top: 20px;
}

.social{
    margin:10px 0px;
}

.footer-logo img{
    max-width: 36px;
    max-height: 36px;
    margin: 0px 4px;
}

.footer-logo .footer-logoi img{
    max-width: 20px;
    max-height: 20px;
    margin: 0px 1px;
}

.f2_about{
    display: flex;
    justify-content: space-evenly;
    gap:32px;
}

.f3{
    border-top: 1px solid white;
    text-align: center;
    padding-top: 24px;
    font-family: 'Poppins';
font-style: normal;
font-weight: 400;
font-size: 16px;
line-height: 24px;
/* identical to box height */


color: #FFFFFF;
}