
.logo{
    display: flex;
    align-items: center;
}

.logo img{
    max-width: 100px;
    max-height: 100px;
  }
  
  .navbar{
    display: flex;
    padding: 12px 26px;
    justify-content: space-between;
    margin: 20px 0px;
    background: #FFFFFF;
box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.15);
border-radius: 16px;
  }

  .navbar_links ul{
    display: flex;
    gap:18px;
    font-size: 15px;
    line-height: 30px;
  }

  .navbar_links ul li a{
    color: #333333;
    font-family: 'Poppins', sans-serif;
    font-weight: 500;
  }

  .navbar .btn{
    background-color: #43DDE6;
    color:#333333;
    padding: 2px 12px;
    border-radius: 44px;
  }